export default {
  name: 'runMission',
  data () {
    return {
      tag: this.Run
    }
  },
  props: {
    missionList: []
  },
  methods: {
    goMissionDetail (missionName, missionID) {
      sessionStorage.setItem('missionName', missionName)
      sessionStorage.setItem('missionID', missionID)
      this.$router.push('/missionDetail')
    }
  }
}



