import Cookies from 'js-cookie'
import MissionList from './components/list'
export default {
  name: 'index',
  components: {
    MissionList
  },
  methods: {
    judge () {
      if (!Cookies.get('id')) {
        this.$router.push('/loginError')
      }
    }
  },
  created () {
    this.judge()
  }
}



