import axios from 'axios/index'
import RunMission from './runMission'
import FinishedMission from './finishedMission'
export default {
  name: 'missionList',
  components: {
    RunMission,
    FinishedMission
  },
  data () {
    return {
      missionList: [],
      activeIndex: '1',
      Run: '',
      Finished: ''
    }
  },
  methods: {
    getMissionManager () {
      // axios.get('/static/testData/gain.json')
      axios.get(this.url + '/mission/manager/' + localStorage.getItem('id'))
        .then(this.getMissionListSucc)
    },
    getMissionOwner () {
      axios.get(this.url + '/mission/owner/' + localStorage.getItem('id'))
        .then(this.getMissionListSucc)
    },
    getMissionLeader () {
      axios.get(this.url + '/mission/leader/' + localStorage.getItem('id'))
        .then(this.getMissionListSucc)
    },
    getMissionList () {
      axios.get(this.url + '/mission/all')
        .then(this.getMissionListSucc)
    },
    getMissionListSucc (res) {
      if (res.status === 200) {
        res = res.data
        let list = this.missionList.concat(res)
        const arr = new Map()
        this.missionList = list.filter((list) => !arr.has(list.id) && arr.set(list.id, 1))
      }
    },
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    showRun () {
      this.Run = true
      this.Finished = false
    },
    showFinished () {
      this.Finished = false
      this.Finished = true
      this.Run = false
    },
    showNew () {
      this.Run = false
      this.Finished = false
    }
  },
  mounted () {
    this.Run = true
    let level = parseInt(localStorage.getItem('level'))
    // let level = this.PI
    if (level === this.PI) this.getMissionManager()
    else if (level === this.PR) {
      this.getMissionManager()
      this.getMissionLeader()
    } else if (level === this.student) this.getMissionOwner()
    else this.getMissionList()
  }
}



